					/>
				</g>
			</svg>
		</div>
	</div>
</template>

<script>
export default {
	name: "CampaignPlate",
	props: {
		name: String,
		campaignId: Number,
		isActive: Boolean,
	},
	data() {
		return {};
	},
	methods: {
		setActive() {
			this.$emit("setActiveCampaign", this.campaignId);
		},

		removeCampaign() {
			this.$emit("removeCampaign", this.campaignId);
		}
	},
};
</script>

<style scoped>
.campaign {
	box-sizing: border-box;
	padding: 10px;

	margin-bottom: 5px;

	cursor: pointer;

	position: relative;
}

.campaign.active {
	background-color: turquoise;
}

.campaign__remove {
	position: absolute;
	top: 5px;
	right: 5px;

	width: 20px;
	height: 20px;

	cursor: pointer;
}

.campaign__remove svg {
	width: 20px;
	height: 20px;
}

.campaign__remove path {
    transition: 0.5s;
    fill: black;
}

.campaign__remove:hover path {
    fill: red;
    transition: 0.5s;
}
</style>
